import { errorToS } from "../core/error/Error"
import { run } from "./info/InfoCLI"

process.on("unhandledRejection", err =>
  console.error("UNHANDLED REJECTION", errorToS(err))
)

process.on("uncaughtException", err =>
  console.error("UNCAUGHT EXCEPTION", errorToS(err))
)

void run()
